import { Component, OnInit } from "@angular/core";

import { LogService } from "../../services/log.service";

import { Log } from "../../models/Log";

@Component({
  selector: "app-log-form",
  templateUrl: "./log-form.component.html",
  styleUrls: ["./log-form.component.css"]
})
export class LogFormComponent implements OnInit {
  // id: string;
  // text: string;
  // date: any;
  log: Log = this.logService.getlogNull();

  constructor(private logService: LogService) {}

  ngOnInit() {
    // Subscribe to the selectedLog observable
    this.logService.selectedLog.subscribe(log => {
      if (log.id !== null) {
        this.log = log;
      } else {
        this.log = log;
        // this.log = Object.assign({}, log);
      }
    });
  }

  onSubmit() {
    // Check if new log
    if (!this.log.id) {
      this.log.id = this.generateId();
      this.log.date = new Date();
      // Add log
      this.logService.addLog(this.log);
    } else {
      // Create log to be updated
        this.log.date = new Date()
      // Update log
      this.logService.updateLog(this.log);
    }

    // Clear state
    this.initLog();
  }

  initLog() {
    this.log = this.logService.getlogNull();
    this.logService.clearNext();
  }

  generateId() {
    return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(/[xy]/g, function(c) {
      var r = (Math.random() * 16) | 0,
        v = c == "x" ? r : (r & 0x3) | 0x8;
      return v.toString(16);
    });
  }
}



