import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
// BehaviorSubject は初期値もしくは最新値を記憶し、子 Observer が subscribe したタイミングもしくは Subject が next()したタイミングでその最新の値を流すイベントを発火します。

import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';

import { Log } from '../models/Log';

@Injectable()
export class LogService {
  logs: Log[];
  logNull: Log = { id: null, text: null, date: null };

  private logSource = new BehaviorSubject<Log>(this.getlogNull());
  selectedLog = this.logSource.asObservable();

  private clearSource = new BehaviorSubject<boolean>(true);
  clearSelected = this.clearSource.asObservable();

  constructor() {
    // this.logs = [
    //   {id: '1', text: 'Generated components', date: new Date('12/26/2017 12:54:23')},
    //   {id: '2', text: 'Added Bootstrap', date: new Date('12/27/2017 9:33:13')},
    //   {id: '3', text: 'Added logs component', date: new Date('12/27/2017 12:00:23')}
    // ]

    this.logs = [];
  }

  getlogNull(): Log {
    return Object.assign({}, this.logNull);
  }

  getLogs(): Observable<Log[]> {
    if (localStorage.getItem("logs") === null) {
      this.logs = [];
    } else {
      this.logs = JSON.parse(localStorage.getItem("logs"));
    }

    // console.log(this.logs.sort((a, b) => b.date - a.date));
    return of(
      this.logs.sort((a, b) => {
        return b.date - a.date; // desc
      })
    );
  }

  logNext(log: Log) {
    this.logSource.next(log);
  }

  addLog(log: Log) {
    this.logs.unshift(log);

    // Add to local storage
    localStorage.setItem("logs", JSON.stringify(this.logs));
  }

  updateLog(log: Log) {
    this.logs.forEach((cur, index) => {
      if (log.id === cur.id) {
        this.logs.splice(index, 1);
      }
    });
    this.logs.unshift(log);
    localStorage
      .setItem("logs", JSON.stringify(this.logs));
  }

  deleteLog(log: Log) {
    this.logs.forEach((cur, index) => {
      if (log.id === cur.id) {
        this.logs.splice(index, 1);
      }
    });
    localStorage.setItem("logs", JSON.stringify(this.logs));
  }

  clearNext() {
    this.clearSource.next(true);
  }
}



