import { Component, OnInit } from "@angular/core";
import { Subject } from "rxjs/Subject";

@Component({
  selector: "app-login",
  templateUrl: "./login.component.html",
  styleUrls: ["./login.component.css"]
})
export class LoginComponent implements OnInit {
  public loginSubject: Subject<number> = new Subject(); // 変更
  public loginState = this.loginSubject.asObservable(); // 追加
  public count = 0; // 追加

  constructor() {}

  ngOnInit() {
    this.loginSubject.subscribe((data: number) => {
      // this.loginState.subscribe((data: number) => {
      // 変更
      console.log(data);
    });
  }

  clickNext() {
    // 追加
    this.count++;
    this.loginSubject.next(this.count);
  }
}



